@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro&display=swap);
.nav-bar {
  background-color: #1e1e24;
  width: 100%;
  margin: 0 auto;
  overflow: hidden;
  position: fixed;
  z-index: 5000;
}

ol {
  list-style-type: none;
  margin: auto;
  width: 35%;
  padding: 0;
  display: flex;
  justify-content: space-around;
}

.nav-item {
  /* border: 1px solid red; */
  float: left;
  display: block;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 18px;
  cursor: pointer;
  border-radius: 3px;
}

.nav-item:hover {
  background-color: #c0d8e0;
  color: #484538;
}

.icon {
  display: none;
}

@media screen and (max-width: 600px) {
  .nav-bar {
    width: 100%;
    position: relative;
    display: none;
    /* background-color: inherit; */
  }
  ol {
    width: 100%;
    display: block;
  }
  .nav-item {
    display: none;
  }

  .icon {
    float: right;
    display: inline-block;
  }
  .display .nav-item {
    float: none;
    display: block;
    text-align: center;
  }

  .display .icon {
    position: absolute;
    top: 0;
    right: 0;
  }
}

.home-content {
  margin: auto auto;
  position: relative;
  text-align: center;
  width: 50%;
  height: 101vh;
  padding-top: 10%;
  text-align: center;
}

.home-content h1 {
  font-size: 4em;
}

.home-content p {
  font-size: 1.5em;
  margin-bottom: 10px;
}

.social-links a {
  display: inline-block;
  margin: 10px;
  color: inherit;
  vertical-align: middle;
  width: 1em;
  height: 1em;
  border-radius: 30px;
}

.fa {
  text-decoration: none;
  text-align: center;
  font-size: 22px;
  border: none;
  border-radius: 50%;
}

#arrow-down {
  margin-top: 8em;
  width: 1em;
  height: 1em;
  margin: 6em auto;
  display: block;
  cursor: pointer;
}
#home {
  background-image: url(/portfolio/static/media/home.efdba046.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
#wave {
  -webkit-animation-name: wave;
          animation-name: wave;
  display: inline-block;
  -webkit-animation-duration: 5s;
          animation-duration: 5s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}
@-webkit-keyframes wave {
  0% {
    transform: rotate(0deg);
  }
  10% {
    transform: rotate(-10deg);
  }
  20% {
    transform: rotate(12deg);
  }
  30% {
    transform: rotate(-10deg);
  }
  40% {
    transform: rotate(9deg);
  }
  50% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(0deg);
  }
}
@keyframes wave {
  0% {
    transform: rotate(0deg);
  }
  10% {
    transform: rotate(-10deg);
  }
  20% {
    transform: rotate(12deg);
  }
  30% {
    transform: rotate(-10deg);
  }
  40% {
    transform: rotate(9deg);
  }
  50% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

@media screen and (max-width: 600px) {
  .home-content {
    width: 100%;
  }
  .home-content h1 {
    font-size: 2em;
    margin-top: 3em;
    padding: 10px;
  }
  .home-content p {
    font-size: 1em;
    padding: 10px;
  }
}

.about-container {
