}

#profile {
  -webkit-filter: brightness(100%);
          filter: brightness(100%);
  width: 150px;
  height: 150px;
  border-radius: 50%;
  margin-right: auto;
  /* display: block; */
}

#download-button {
  font-size: 1.3em;
  padding: 5px;
  margin-top: 1em;
  border-radius: 5px;
  outline: none;
  border: none;
