    padding: 5px;
  }
}

@media screen and (max-width: 600px) {
  .content {
    flex-wrap: wrap;
    margin: 0;
  }

  .content-image {
    padding: 1em;
  }

  .content-text {
    padding: 1em;
  }

  #profile {
